*{
    margin: 0;
    padding: 0;
}
.navbar{
    background-color: #2C272E;
    padding: 10px;
    font-size: 20px;
    color: white;
    padding-left: 20px;
}
.productListContainer{
    padding: 50px;
    display: grid; 
  grid-template-columns: 1fr 1fr 1fr; 
  grid-template-rows: 1fr 1fr 1fr ; 
  gap: 13px 58px; 
  grid-template-areas: 
    ". . ."
    ". . ."
    ". . ."
    ". . ."; 
}
.product_container{
    margin: 10px 20px;
    padding: 0px 10px 10px 10px;
    border-radius: 20px;
    box-shadow: 0px 0px 10px black;
}
.product_container button{
    border-radius: 4px;
    padding:0px 7px;
    font-size: 20px;
    margin-top: 5px;
    font-weight: 700;
    color: aliceblue;
}
.button-container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.footer{
    background-color: #2C272E;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    width: 100%;
}
.footer button{
    padding: 3px 10px;
    font-weight: 600;
    
}
.form-container{
    display: flex;
    padding: 50px;
    justify-content: space-between;
    align-items: center;
}
.form-container input{
    padding: 3px 10px;
}
.form-container label{
    position:absolute;
    margin-top: -20px;
}
.form-container button{
    background-color: green;
    font-weight: 600;
    padding: 3px 10px;
    color: aliceblue;
    border-radius: 2px;
    border: 2px solid green;
}
@media screen and (max-width : 1100px ){
.productListContainer{
    grid-template-columns: 1fr 1fr; 
    grid-template-areas: 
      ". ."
      ". ."
      ". ."
      ". ."; 
}
}
@media screen and (max-width : 800px ){
    .productListContainer{
        grid-template-columns: 1fr; 
        grid-template-areas: 
          ".";
    }
}
@media screen and (max-width : 500px ){
    .productListContainer{
        padding: 10px;
        padding-bottom: 50px;
    }
    .button-container{
        font-size: 14px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
    }
    .footer{
        height: 40px;
        align-items: center;
    }
    .form-container{
        flex-direction: column;
        align-items: center;
        padding: 10px;
    }
    .form-container label{
        position: relative;
    }
    .form-container input{
        margin-left: 10px;
    }
    .form-container button{
        width: 50%;
        margin-top: 20px;
    }
    .price-form{
        margin-top: 10px;
        margin-right: -6px;
    }
}